// js/simulationCore.js
class SimulationCore {
    constructor(gridResolution, mapGenerator, settlementManager) {
        this.gridResolution = gridResolution;
        this.mapGenerator = mapGenerator;
        this.settlementManager = settlementManager;

        this.heightMap = null;
        this.waterMap = null;
        this.cloudMap = null;
        this.riskMap = null;
        
        // Simulation parameters optimizados para mejor simulaci√≥n de agua
        this.RAINFALL_AMOUNT = 0.5; // Aumentado para m√°s lluvia
        this.CLOUDINESS_FACTOR = 1.0; // M√°xima densidad de nubes
        this.FLOW_RATE_FACTOR = 0.5; // Mayor velocidad de flujo
        this.BASE_INFILTRATION_RATE = 0.00001; // Infiltraci√≥n m√≠nima
        this.MAX_WATER_HEIGHT = 10.0; // Mayor capacidad de agua
        this.NUM_RAINDROPS_PER_FRAME = 2000; // Menos gotas por frame para mejor rendimiento
        this.FLOW_SUB_STEPS = 3; // Reducido para eficiencia (antes 2, ahora 3, pero se usar√° 2 en _simulateWaterFlow)
        this.cloudOffset = 0;
        this.cloudSpeed = 0.008;

        this.initializeMaps();
    }

    initializeMaps() {
        this.heightMap = this.mapGenerator.generateHeightMap();
        this.waterMap = new Float32Array(this.gridResolution * this.gridResolution).fill(0);
        this.cloudMap = this.mapGenerator.generateCloudMap(this.cloudOffset);
        // computeRiskMap ahora es una funci√≥n pura que toma waterMap y heightMap
        this.riskMap = this._computeRiskMap(this.waterMap, this.heightMap);
        this.settlementManager.heightMap = this.heightMap; // Ensure settlement manager has updated heightmap
        this.settlementManager.generateSettlements(); // Generate settlements based on new map
    }

    resetSimulation() {
        this.initializeMaps();
    }

    // Funciones de c√°lculo puro (sin modificar el estado de la clase directamente)

    /**
     * Computes the risk map based on current water and height maps.
     * @param {Float32Array} waterMap - The current water map.
     * @param {Float32Array} heightMap - The current height map.
     * @returns {Array<Object>} The computed risk map.
     */
    _computeRiskMap(waterMap, heightMap) {
        const riskMap = [];
        const len = this.gridResolution * this.gridResolution;
        for (let i = 0; i < len; i++) {
            const water = waterMap[i];
            const terrain = heightMap[i];

            // Flood risk: Proportional to water depth above a certain threshold of terrain
            let floodRisk = Math.max(0, water - (terrain * 0.1)); // Water above 10% of terrain height
            floodRisk = Math.min(1, floodRisk * 5); // Scale and cap at 1

            // Landslide risk: Higher terrain + some water makes it risky
            let landslideRisk = 0;
            if (terrain > 0.5 && water > 0.05) { // Only high terrain and some water causes risk
                landslideRisk = (terrain - 0.5) * 2 + (water - 0.05) * 3; // Terrain contribution + water contribution
            }
            landslideRisk = Math.min(1, landslideRisk); // Cap at 1

            riskMap.push({ flood: floodRisk, landslide: landslideRisk });
        }
        return riskMap;
    }

    /**
     * Applies rainfall and infiltration to the water map.
     * @param {Float32Array} currentWaterMap - The water map before rainfall and infiltration.
     * @param {Float32Array} heightMap - The height map.
     * @param {Float32Array} cloudMap - The cloud map.
     * @param {number} rainfallAmount - The base amount of rainfall.
     * @param {number} baseInfiltrationRate - The base rate of water infiltration into terrain.
     * @param {number} maxWaterHeight - The maximum allowed water height.
     * @param {number} numRaindropsPerFrame - Number of raindrops to simulate per frame.
     * @returns {Float32Array} The new water map after rainfall and infiltration.
     */
    _applyRainfallAndInfiltration(currentWaterMap, heightMap, cloudMap, rainfallAmount, baseInfiltrationRate, maxWaterHeight, numRaindropsPerFrame) {
        const newWaterMap = new Float32Array(currentWaterMap); // Create a new array for immutability
        const len = newWaterMap.length;

        // Simulaci√≥n de gotas: solo caen donde hay nubes, y la cantidad depende de la densidad de nubes
        for (let i = 0; i < numRaindropsPerFrame; i++) {
            const idx = (Math.random() * len) | 0;
            const cloudDensity = cloudMap[idx];
            // Solo dejar caer gota si hay nube significativa
            if (cloudDensity > 0.2) {
                // La cantidad de agua depende de la densidad de la nube
                const rain = rainfallAmount * cloudDensity;
                newWaterMap[idx] += rain;
            }
        }

        // Infiltraci√≥n y evaporaci√≥n
        for (let i = 0; i < len; i++) {
            let currentWater = newWaterMap[i];
            let effectiveInfiltration = baseInfiltrationRate;

            // Infiltration logic from original, applied to newWaterMap
            if (currentWater > 1.0) effectiveInfiltration *= 0.1;
            else if (heightMap[i] > 0.7) effectiveInfiltration *= 2.0;

            currentWater = Math.max(0, currentWater - effectiveInfiltration);
            // Evaporaci√≥n m√≠nima
            currentWater *= 0.99998;
            newWaterMap[i] = Math.min(currentWater, maxWaterHeight);
        }
        return newWaterMap;
    }

    /**
     * Simulates water flow across the map.
     * @param {Float32Array} currentWaterMap - The water map before flow simulation.
     * @param {Float32Array} heightMap - The height map.
     * @param {number} gridResolution - The resolution of the grid (width/height).
     * @param {number} flowRateFactor - Factor controlling the speed of water flow.
     * @returns {Float32Array} The new water map after flow simulation.
     */
    _simulateWaterFlow(currentWaterMap, heightMap, gridResolution, flowRateFactor) {
        const w = gridResolution;
        const h = gridResolution;
        let waterIter = new Float32Array(currentWaterMap); // Start with a copy for iterative updates
        let next_water_buffer = new Float32Array(currentWaterMap.length); // Buffer for next state

        const MAX_ITER = 2; // Only 2 sub-pasos para eficiencia
        const MIN_FLOW = 0.0001;
        const FLOW_RATE = 0.18 * flowRateFactor;

        for (let iter = 0; iter < MAX_ITER; iter++) {
            // Copy current water state to buffer before calculating next state
            next_water_buffer.set(waterIter);

            for (let y = 0; y < h; y++) {
                for (let x = 0; x < w; x++) {
                    const idx = y * w + x;
                    const h0 = heightMap[idx] + waterIter[idx]; // Use waterIter for current height
                    
                    const neighbors = [
                        [x, y - 1], // N
                        [x, y + 1], // S
                        [x - 1, y], // O
                        [x + 1, y]  // E
                    ];

                    for (const [nx, ny] of neighbors) {
                        if (nx < 0 || nx >= w || ny < 0 || ny >= h) continue;
                        const nidx = ny * w + nx;
                        const h1 = heightMap[nidx] + waterIter[nidx]; // Use waterIter for neighbor height

                        if (h0 > h1 + MIN_FLOW) {
                            // Flujo proporcional a la diferencia de altura
                            // Ensure flow does not exceed available water at current cell
                            const flow = Math.min((h0 - h1) * FLOW_RATE, waterIter[idx]); 
                            
                            if (flow > MIN_FLOW) {
                                next_water_buffer[idx] -= flow;
                                next_water_buffer[nidx] += flow;
                            }
                        }
                    }
                }
            }
            // Update waterIter for the next sub-step
            waterIter.set(next_water_buffer);
        }
        return waterIter; // Return the final water map after all sub-steps
    }
    
    // Funci√≥n de c√°lculo de escorrent√≠a, dise√±ada para ser funcional
    /**
     * Calculates and applies drainage and basin accumulation.
     * @param {Float32Array} currentWaterMap - The water map before drainage.
     * @param {Float32Array} heightMap - The height map.
     * @param {number} gridResolution - The resolution of the grid.
     * @returns {Float32Array} The new water map after drainage.
     */
    _applyDrainage(currentWaterMap, heightMap, gridResolution) {
        const newWaterMap = new Float32Array(currentWaterMap);
        const SECONDS_PER_FRAME = 0.1; // Assumed from original snippet. This should ideally be passed as a param or a constant.
        const w = gridResolution;
        const h = gridResolution;

        for (let y = 0; y < h; y++) {
            for (let x = 0; x < w; x++) {
                const idx = y * w + x;
                const cellWater = newWaterMap[idx];
                const cellHeight = heightMap[idx];

                // Drainage proportional to height
                const drainage = cellHeight * 0.15 * SECONDS_PER_FRAME;
                newWaterMap[idx] = Math.max(0, cellWater - drainage);

                // Determine downstream cell based on height for simple basin accumulation
                // This is a simplified approach. A more robust solution would involve proper flow direction algorithms.
                let minNeighborHeight = Infinity;
                let downstreamIdx = -1;

                const neighbors = [
                    [x, y - 1], // N
                    [x, y + 1], // S
                    [x - 1, y], // O
                    [x + 1, y]  // E
                ];

                for (const [nx, ny] of neighbors) {
                    if (nx >= 0 && nx < w && ny >= 0 && ny < h) {
                        const nidx = ny * w + nx;
                        const neighborTotalHeight = heightMap[nidx] + newWaterMap[nidx]; // Consider water level too
                        if (neighborTotalHeight < minNeighborHeight) {
                            minNeighborHeight = neighborTotalHeight;
                            downstreamIdx = nidx;
                        }
                    }
                }
                
                // Accumulate in cuencas (if a downstream cell is found)
                if (downstreamIdx !== -1 && heightMap[idx] + cellWater > minNeighborHeight + 0.001) { // Only flow if current cell is higher
                     // Apply a fraction of the drained water to the downstream cell
                    newWaterMap[downstreamIdx] += drainage * 0.7; // 70% of drained water goes downstream
                }
            }
        }
        return newWaterMap;
    }


    updateSimulation() {
        // Actualizar el offset de la nube y regenerar el mapa de nubes
        this.cloudOffset += this.cloudSpeed;
        this.cloudMap = this.mapGenerator.generateCloudMap(this.cloudOffset);

        // Paso 1: Aplicar lluvia e infiltraci√≥n
        // Se llama a la funci√≥n pura y se asigna el resultado a this.waterMap
        this.waterMap = this._applyRainfallAndInfiltration(
            this.waterMap,
            this.heightMap,
            this.cloudMap,
            this.RAINFALL_AMOUNT,
            this.BASE_INFILTRATION_RATE,
            this.MAX_WATER_HEIGHT,
            this.NUM_RAINDROPS_PER_FRAME
        );

        // Paso 2: Simular el flujo de agua
        // Se llama a la funci√≥n pura y se asigna el resultado a this.waterMap
        this.waterMap = this._simulateWaterFlow(
            this.waterMap,
            this.heightMap,
            this.gridResolution,
            this.FLOW_RATE_FACTOR
        );

        // Paso 3: Aplicar drenaje y acumulaci√≥n en cuencas (el snippet de abajo)
        // Se llama a la funci√≥n pura y se asigna el resultado a this.waterMap
        this.waterMap = this._applyDrainage(
            this.waterMap,
            this.heightMap,
            this.gridResolution
        );

        // Paso 4: Recalcular el mapa de riesgo con el nuevo waterMap
        // Se llama a la funci√≥n pura y se asigna el resultado a this.riskMap
        this.riskMap = this._computeRiskMap(this.waterMap, this.heightMap);
    }

    // Getters for renderer to access simulation data
    getMaps() {
        return {
            heightMap: this.heightMap,
            waterMap: this.waterMap,
            cloudMap: this.cloudMap,
            riskMap: this.riskMap
        };
    }

    getSettings() {
        return {
            cloudinessFactor: this.CLOUDINESS_FACTOR,
            showClouds: this.SHOW_CLOUDS, // This needs to be set from controls
            baseInfiltrationRate: this.BASE_INFILTRATION_RATE
        };
    }

    // Setters for controls to modify simulation parameters
    setRainfallAmount(amount) { this.RAINFALL_AMOUNT = amount; }
    setCloudinessFactor(factor) { this.CLOUDINESS_FACTOR = factor; }
    setFlowRateFactor(factor) { this.FLOW_RATE_FACTOR = factor; }
    setBaseInfiltrationRate(rate) { this.BASE_INFILTRATION_RATE = rate; }
    setShowClouds(show) { this.SHOW_CLOUDS = show; } // For the renderer to know if clouds should be drawn
}

export { SimulationCore };